<template>
	<div class="transaction-search-transaction-list">
		<mt-cell class="product-title" title="客户委托交易查询："></mt-cell>
		<div class="products">
			<a @click="details(item.id)" v-for="item in products" class="product-item" href="javascript:;">
				<mt-cell title="产品代码：" :value="item.code"></mt-cell>
				<mt-cell title="产品名称：" :value="item.name"></mt-cell>
				<mt-cell title="交易类型：" :value="item.type"></mt-cell>
				<mt-cell title="申请金额：" :value="item.money"></mt-cell>
				<mt-cell title="申请份额：" :value="item.part"></mt-cell>
				<mt-cell title="交易状态：" :value="item.status"></mt-cell>
				<mt-cell title="交易日期：" :value="item.date"></mt-cell>
			</a>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			products: [],
			detail: {}
		};
	},
	methods: {
		details(id) {
			this.$router.push('/finance-products/search/transaction/detail/' + id);
		},
		back() {
			history.back();
		},
		getSearchTransactionForAccount(detail) {
			service.getSearchTransactionForAccount({
				account: detail.account,
				type: detail.type,
				beginDate: detail.beginDate,
				endDate: detail.endDate
			}).then(data => {
				this.products = data.data.data;
			});
		}
	},
	created() {
		this.detail = Object.assign({}, this.$store.state.searchTransaction.detail);
		this.getSearchTransactionForAccount(this.detail);
	}
};

</script>

<style lang="scss">
	
.transaction-search-transaction-list{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


