<template>
	<div class="transaction-search-transaction-detail">
		<mt-cell title="客户委托交易查询："></mt-cell>
		<mt-cell title="理财账户：" :value="detail.account"></mt-cell>
		<mt-cell title="理财流水号：" :value="detail.number"></mt-cell>
		<mt-cell title="客户名称" :value="detail.custom"></mt-cell>
		<mt-cell title="客户类型：" :value="detail.customType"></mt-cell>
		<mt-cell title="证件类型：" :value="detail.idCardType"></mt-cell>
		<mt-cell title="证件号码：" :value="detail.idCard"></mt-cell>
		<mt-cell title="交易类型：" :value="detail.transType"></mt-cell>
		<mt-cell title="产品代码：" :value="detail.code"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.name"></mt-cell>
		<mt-cell title="申请金额：" :value="detail.applyMoney"></mt-cell>
		<mt-cell title="申请份额：" :value="detail.applyPart"></mt-cell>
		<mt-cell title="交易信息：" :value="detail.transMessage"></mt-cell>
		<mt-cell title="交易日期：" :value="detail.transDate"></mt-cell>
		<mt-cell title="交易渠道：" :value="detail.transChannel"></mt-cell>
		<mt-cell title="交易状态：" :value="detail.transStatus"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				id: '',
				account: '',
				number: '',
				custom: '',
				customType: '',
				idCardType: '',
				idCard: '',
				transType: '',
				code: '',
				name: '',
				applyMoney: '',
				applyPart: '',
				transMessage: '',
				transDate: '',
				transChannel: '',
				transStatus: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/finance-products/search/transaction');
		},
		getSearchTransactionDetail(id) {
			service.getSearchTransactionDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getSearchTransactionDetail(this.$route.params.id);
	}
};

</script>


