<template>
	<div class="transaction-product-apply-list">
		<mt-cell class="product-title" title="产品信息："></mt-cell>
		<div class="products">
			<a @click="detail(item.id)" v-for="item in products" class="product-item" href="javascript:;">
				<mt-cell title="产品名称：" :value="item.name"></mt-cell>
				<mt-cell title="产品代码：" :value="item.code"></mt-cell>
				<mt-cell title="风险等级：" :value="item.grade"></mt-cell>
				<mt-cell title="预期年化收益率：" :value="item.profit"></mt-cell>
				<mt-cell title="理财期限：" :value="item.day"></mt-cell>
			</a>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			products: []
		};
	},
	methods: {
		detail(id) {
			this.$router.push('/finance-products/transaction/product-apply/agree/' + id);
		},
		back() {
			history.back();
		},
		getProductApplyForAccount(id) {
			service.getProductApplyForAccount({
				id: id
			}).then(data => {
				this.products = data.data.data;
			});
		}
	},
	created() {
		this.getProductApplyForAccount(this.$route.params.id);
	}
};

</script>

<style lang="scss">
	
.transaction-product-apply-list{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


