<template>
	<div class="transaction-search-product-detail">
		<mt-cell title="产品详细信息："></mt-cell>
		<mt-cell title="产品代码：" :value="detail.code"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.name"></mt-cell>
		<mt-cell title="是否可赎回：" :value="detail.redeem"></mt-cell>
		<mt-cell title="产品持有金额：" :value="detail.money"></mt-cell>
		<mt-cell title="产品持有份额：" :value="detail.part"></mt-cell>
		<mt-cell title="产品状态" :value="detail.status"></mt-cell>
		<mt-cell title="产品募集开始日：" :value="detail.startDay"></mt-cell>
		<mt-cell title="产品募集结束日：" :value="detail.endDay"></mt-cell>
		<mt-cell title="产品成立日：" :value="detail.setDay"></mt-cell>
		<mt-cell title="产品到期日：" :value="detail.lastDay"></mt-cell>
		<mt-cell title="预期年化收益率：" :value="detail.profit"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				code: '',
				name: '',
				redeem: '',
				money: '',
				part: '',
				status: '',
				startDay: '',
				endDay: '',
				setDay: '',
				lastDay: '',
				profit: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/finance-products/search/product');
		},
		getSearchProductDetail(id) {
			service.getSearchProductDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getSearchProductDetail(this.$route.params.id);
	}
};

</script>


