<template>
	<div class="finance-products-risk-assess-result">
		<mt-cell title="风险评估结果："></mt-cell>
		<mt-cell title="客户风险承受等级：" :value="detail.grade"></mt-cell>
		<mt-cell title="风险等级有效截止日期：" :value="detail.endDate"></mt-cell>
		<mt-cell class="colorRed" title="风险等级评估得分：" :value="detail.score"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回首页</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
				grade: '',
				endDate: '',
				score: ''
			}
		};
	},
	methods: {
        back() {
        	this.$router.push('/finance-products/risk');
        },
        getUserRisk(id) {
        	service.getUserRisk({
        		id: id
        	})
        	.then(data => {
        		this.detail = data.data.data;
        	});
        }
	},
	created() {
		this.getUserRisk(this.$route.params.id);
	}
};

</script>

<style lang="scss">
	
.finance-products-risk-assess-result{
	.mint-cell-title{
		width: 5rem;
	}
	.colorRed{
		.mint-cell-value{
			color: red;
		}
	}
}

</style>


