<template>
	<div class="transaction-search-custom-detail">
		<mt-cell title="客户信息查询："></mt-cell>
		<mt-cell title="客户名称：" :value="detail.name"></mt-cell>
		<mt-cell title="证件类型：" :value="detail.type"></mt-cell>
		<mt-cell title="证件号码：" :value="detail.idCard"></mt-cell>
		<mt-cell title="客户风险等级：" :value="detail.grade"></mt-cell>
		<mt-cell title="风险评级有效日期：" :value="detail.date"></mt-cell>
		<mt-cell title="手机号码：" :value="detail.mobile"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				name: '',
				type: '',
				idCard: '',
				grade: '',
				date: '',
				mobile: ''
			}
		};
	},
	methods: {
		back() {
			history.back();
		},
		getSearchCustomDetail(account) {
			service.getSearchCustomDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getSearchCustomDetail(this.$route.params.account);
	}
};

</script>


