<template>
	<div class="transaction-product-redeem-redeem">
		<mt-cell title="理财赎回："></mt-cell>
		<mt-cell title="产品代码：" :value="detail.code"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.name"></mt-cell>
		<mt-cell title="产品持有份额：" :value="detail.part"></mt-cell>
		<mt-cell title="产品到期日：" :value="detail.lastDay"></mt-cell>
		<mt-cell title="预期年化收益率：" :value="detail.profit"></mt-cell>
		<mt-cell title="理财账户：" :value="detail.account"></mt-cell>
		<mt-field type="number" v-model="redeemMoney" label="赎回份额："></mt-field>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">确定</mt-button>
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				code: '',
				name: '',
				part: '',
				lastDay: '',
				profit: '',
				account: ''
			},
			redeemMoney: '',
			cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			if(!this.redeemMoney){
				Toast({
                    message: '请输入赎回份额！',
                    iconClass: 'error',
                    duration: 1000
                });
                return;
			}
			service.redeemProductSubmit({
				redeemMoney: this.redeemMoney,
				id: this.detail.id
			}).then(data => {
				this.$router.push(`/finance-products/transaction/product-redeem/success/${this.detail.id}`);
			});
		},
		back() {
			history.back();
		},
		getProductRedeemRedeem(id) {
			service.getProductRedeemRedeem({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getProductRedeemRedeem(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.transaction-product-redeem-redeem{
	.mint-cell-title{
		width: 5rem;
	}
}

</style>


