<template>
	<div class="transaction-search-transaction">
		<mt-cell title="客户委托交易查询："></mt-cell>
        <mt-cell @click.native="showSelectorPaymentAccount()" title="选择账号：" placeholder="" v-model="detail.account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        <mt-cell class="mint-select" title="交易类型：" is-link>
            <select class="mint-field-core" v-model="detail.type">
                <option v-for="item in seletsType" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
        <mt-cell title="起始日期：" :value="detail.beginDate"></mt-cell>
		<mt-cell title="结束日期：" :value="detail.endDate"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value="确定"></submit-button>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
				account: '',
				type: '',
				beginDate: '',
				endDate: ''
			},
            submitStatus: false,
            seletsType: [
            	{
            		val: '',
            		desc: '全部'
            	},
                {
                    val: '1',
                    desc: '已交易'
                },
                {
                	val: '0',
                	desc: '已申请'
                }
            ],
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
    watch: {
        detail: {
            handler(item){
                // if(item.account && item.type && item.beginDate && item.endDate){
                if(item.account && item.type){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.checkProductHas(this.detail);
        },
        back() {
        	history.back();
        },
        checkProductHas(detail) {
            service.getSearchTransactionForAccount({
                account: detail.account
            }).then(data => {
                if(!data.data.data || data.data.data.length === 0){
                    var self = this;
                    MessageBox({
                        title: '提示信息',
                        message: '交易失败，没有查询到客户持有产品信息！',
                        confirmButtonText: '确定',
                        closeOnClickModal: false
                    }).then(action => {
                    });
                }else{
                    this.$router.push(`/finance-products/search/transaction/list`);
                }
            });
        }
	},
	created() {
		this.detail = Object.assign({}, this.$store.state.searchTransaction.detail);
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateSearchTransaction', {key: 'detail', value: this.detail});
        }else{
            this.$store.dispatch('updateSearchTransaction', {key: 'detail', value: {account: '', type: '', beginDate: '', endDate: ''}});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.transaction-search-transaction{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


