<template>
	<div class="transaction-order-back-success">
		<mt-cell title="提示信息"></mt-cell>
		<div class="success-contain">
			<mt-cell title="交易成功！"></mt-cell>
			<mt-cell title="申请日期：" :value="detail.applyDate"></mt-cell>
			<mt-cell title="手续费：" :value="detail.fee"></mt-cell>
			<mt-cell title="产品代码：" :value="detail.code"></mt-cell>
			<mt-cell title="产品名称：" :value="detail.name"></mt-cell>
			<mt-cell title="理财账户：" :value="detail.account"></mt-cell>
			<mt-cell title="客户名称：" :value="detail.custom"></mt-cell>
			<mt-cell title="交易金额：" :value="detail.money"></mt-cell>
			<mt-cell title="原交易流水：" :value="detail.number"></mt-cell>
			<mt-cell title="币种：" :value="detail.type"></mt-cell>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>

</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				applyDate: '',
				fee: '',
				code: '',
				name: '',
				account: '',
				custom: '',
				money: '',
				number: '',
				type: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/finance-products/transaction/order-back');
		},
		orderBackSuccess(id) {
			service.orderBackSuccess({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.orderBackSuccess(this.$route.params.id);
	}
};

</script>

<style lang="scss" scoped>

.transaction-order-back-success{
	.mint-cell:after{
		height: 0;
	}
	.success-contain{
		border-top: 1px solid #d9d9d9;
		border-bottom: 1px solid #d9d9d9;
	}
}

</style>


