<template>
	<div class="transaction-search-custom">
		<mt-cell title="客户信息查询："></mt-cell>
        <mt-cell @click.native="showSelectorPaymentAccount()" title="理财账户：" placeholder="" v-model="account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        <div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value="确定"></submit-button>
		</div>
	</div>
</template>

<script>
	
export default{
	data() {
		return {
			account: '',
            submitStatus: false,
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
    watch: {
        'account': function(val) {
            if(val) this.submitStatus = true;
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.$router.push(`/finance-products/search/custom/detail/${this.account}`);
        },
        back() {
        	history.back();
        }
	},
	created() {
		this.account = this.account || this.$store.state.searchCustom.account;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
        	this.$store.dispatch('updateSearchCustom', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateSearchCustom', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.transaction-search-custom{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


