<template>
	<div class="transaction-order-back-list">
		<mt-cell class="product-title" title="交易撤单："></mt-cell>
		<div class="products">
			<a @click="detail(item.id)" v-for="item in products" class="product-item" href="javascript:;">
				<mt-cell title="产品代码：" :value="item.code"></mt-cell>
				<mt-cell title="产品名称：" :value="item.name"></mt-cell>
				<mt-cell title="原交易类型：" :value="item.type"></mt-cell>
				<mt-cell title="产品持有金额：" :value="item.money"></mt-cell>
				<mt-cell title="产品持有份额：" :value="item.part"></mt-cell>
				<mt-cell title="产品成立日：" :value="item.setDay"></mt-cell>
				<mt-cell title="产品到期日：" :value="item.lastDay"></mt-cell>
			</a>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			products: []
		};
	},
	methods: {
		detail(id) {
			this.$router.push(`/finance-products/transaction/order-back/detail/${id}`);
		},
		back() {
			history.back();
		},
		getOrderBackForAccount(account) {
			service.getOrderBackForAccount({
				account: account
			}).then(data => {
				this.products = data.data.data;
			});
		}
	},
	created() {
		this.getOrderBackForAccount(this.$route.params.account);
	}
};

</script>

<style lang="scss">
	
.transaction-order-back-list{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


