<template>
	<div class="transaction-order-back-detail">
		<mt-cell title="产品详细信息："></mt-cell>
		<mt-cell title="理财账户：" :value="detail.account"></mt-cell>
		<mt-cell title="产品代码：" :value="detail.code"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.name"></mt-cell>
		<mt-cell title="原交易类型：" :value="detail.type"></mt-cell>
		<mt-cell title="申请金额：" :value="detail.money"></mt-cell>
		<mt-cell title="申请份额：" :value="detail.part"></mt-cell>
		<mt-cell title="产品到期日：" :value="detail.lastDay"></mt-cell>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<submit-button @submit="submit" :submitStatus="submitStatus" value="确定"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				account: '',
				code: '',
				name: '',
				type: '',
				money: '',
				part: '',
				lastDay: ''
			},
			submitStatus: false,
			cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			service.orderBackSubmit()
			.then(data => {
				this.$router.push(`/finance-products/transaction/order-back/success/${this.$route.params.id}`);
			});
		},
		back() {
			history.back();
		},
		getOrderBackDetail(id) {
			service.getOrderBackDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getOrderBackDetail(this.$route.params.id);
	}
};

</script>


