<template>
	<div class="transaction-product-apply-success">
		<mt-cell title="提示信息"></mt-cell>
		<div class="success-contain">
			<mt-cell title="交易成功！"></mt-cell>
			<mt-cell title="产品代码：" :value="detail.code"></mt-cell>
			<mt-cell title="产品名称：" :value="detail.name"></mt-cell>
			<mt-cell title="申请日期：" :value="detail.applyDate"></mt-cell>
			<mt-cell title="币种：" :value="detail.type"></mt-cell>
			<mt-cell title="交易金额：" :value="detail.money"></mt-cell>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>

</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				code: '',
				name: '',
				applyDate: '',
				type: '',
				money: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/finance-products/transaction/product-apply');
		},
		getProductApplyBuySuccess(id) {
			service.getProductApplyBuySuccess({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getProductApplyBuySuccess(this.$route.params.id);
	}
};

</script>

<style lang="scss" scoped>

.transaction-product-apply-success{
	.mint-cell:after{
		height: 0;
	}
	.success-contain{
		border-top: 1px solid #d9d9d9;
		border-bottom: 1px solid #d9d9d9;
	}
}

</style>


