<template>
	<div class="transaction-product-order-detail">
		<mt-cell title="产品详细信息："></mt-cell>
		<mt-cell title="产品代码：" :value="detail.code"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.name"></mt-cell>
		<mt-cell title="发行人：" :value="detail.person"></mt-cell>
		<mt-cell title="产品状态：" :value="detail.status"></mt-cell>
		<mt-cell title="产品类型：" :value="detail.type"></mt-cell>
		<mt-cell title="是否保本：" :value="detail.keep"></mt-cell>
		<mt-cell title="期限：" :value="detail.day"></mt-cell>
		<mt-cell title="起点金额：" :value="detail.startMoney"></mt-cell>
		<mt-cell title="首次购买最低金额：" :value="detail.firstMoney"></mt-cell>
		<mt-cell title="开始日期：" :value="detail.startDate"></mt-cell>
		<mt-cell title="截止日期：" :value="detail.endDate"></mt-cell>
		<mt-cell title="成立日期：" :value="detail.setDate"></mt-cell>
		<mt-cell title="清盘日期：" :value="detail.clearDate"></mt-cell>
		<mt-cell title="预期年化收益率：" :value="detail.profit"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="next" type="primary">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				code: '',
				name: '',
				person: '',
				status: '',
				type: '',
				keep: '',
				day: '',
				startMoney: '',
				firstMoney: '',
				startDate: '',
				endDate: '',
				setDate: '',
				clearDate: '',
				profit: ''
			}
		};
	},
	methods: {
		next() {
			this.$router.push('/finance-products/transaction/product-order/buy/' + this.$route.params.id);
		},
		back() {
			history.back();
		},
		getProductDetail(id) {
			service.getProductDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getProductDetail(this.$route.params.id);
	}
};

</script>


