<template>
	<div class="transaction-product-order-buy">
		<mt-cell title="产品预约认购："></mt-cell>
		<mt-cell title="理财账户：" :value="detail.account"></mt-cell>
		<mt-cell title="证件号码：" :value="detail.idCard"></mt-cell>
		<mt-cell title="账户可用余额：" :value="detail.money"></mt-cell>
		<mt-cell title="起点金额：" :value="detail.startMoney"></mt-cell>
		<mt-cell title="递增金额：" :value="detail.addMoney"></mt-cell>
		<mt-cell title="首次购买最低金额：" :value="detail.firstMoney"></mt-cell>
		<mt-field type="number" v-model="buyMoney" label="预约认购金额："></mt-field>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">确定</mt-button>
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				account: '',
				idCard: '',
				money: '',
				startMoney: '',
				addMoney: '',
				firstMoney: ''
			},
			buyMoney: '',
			cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			if(!this.buyMoney){
				Toast({
                    message: '请输入认购金额！',
                    iconClass: 'error',
                    duration: 1000
                });
                return;
			}
			service.getProductBuy()
			.then(data => {
				this.$router.push(`/finance-products/transaction/product-order/success/${this.$route.params.id}`);
			});
		},
		back() {
			history.back();
		},
		getProductBuyDetail(id) {
			service.getProductBuyDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getProductBuyDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.transaction-product-order-buy{
	.mint-cell-title{
		width: 5rem;
	}
}

</style>


