<template>
	<div class="transaction-product-order">
		<mt-cell title="产品预约认购："></mt-cell>
        <mt-cell @click.native="showSelectorPaymentAccount()" title="理财账户" placeholder="" v-model="detail.account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<mt-field v-model="detail.code" label="产品代码：" type="text"></mt-field>
		<mt-field v-model="detail.name" label="产品名称："></mt-field>
        <mt-cell class="mint-select" title="投资期限：" is-link>
            <select class="mint-field-core" v-model="detail.day">
                <option v-for="item in seletsItem.days" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
        <mt-cell class="mint-select" title="是否保本：" is-link>
            <select class="mint-field-core" v-model="detail.keep">
                <option v-for="item in seletsItem.keeps" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
        <mt-cell class="mint-select" title="预期年化收益率：" is-link>
            <select class="mint-field-core" v-model="detail.profit">
                <option v-for="item in seletsItem.profits" :value="item.val">
                    {{item.desc}}
                </option>
            </select>
        </mt-cell>
		<div class="field-action">
            <mt-button @click.native="back" type="primary">返回</mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value="确定"></submit-button>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
                account: '',
				code: '',
				name: '',
                day: '',
                keep: '',
                profit: ''
			},
            submitStatus: false,
            seletsItem: {
                days: [
                    {
                        val: '',
                        desc: '不限'
                    }
                ],
                keeps: [
                    {
                        val: '',
                        desc: '不限'
                    }
                ],
                profits: [
                    {
                        val: '',
                        desc: '不限'
                    }
                ]
            },
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
        };
    },
    watch: {
        'detail.account': function(val) {
            if(val) this.submitStatus = true;
        }
    },
	created() {
		this.detail = Object.assign({}, this.$store.state.productOrder.detail);
	},
    methods: {
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.account;
            }, data => {
            });
        },
        submit() {
            this.checkProductHas(this.detail);
        },
        back() {
            history.back();
        },
        checkProductHas(detail) {
            service.getProductForAccount({
                account: detail.account
            }).then(data => {
                if(!data.data.data || data.data.data.length === 0){
                    var self = this;
                    MessageBox({
                        title: '提示信息',
                        message: '交易失败，没有查到产品信息',
                        confirmButtonText: '确定',
                        closeOnClickModal: false
                    }).then(action => {
                    });
                }else{
                    this.$router.push('/finance-products/transaction/product-order/list');
                }
            });
        }
    },
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateProductOrder', {key: 'detail', value: this.detail});
        }else{
            this.$store.dispatch('updateProductOrder', {key: 'detail', value: {account: '', code: '', name: '', day: '', keep: '', profit: ''}});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.transaction-product-order{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


