<template>
	<div class="transaction-product-order-agree">
		<mt-cell title="风险提示书："></mt-cell>
		<mt-cell title="风险揭示书：" is-link></mt-cell>
		<mt-cell title="理财产品说明书：" is-link></mt-cell>
		<mt-cell title="客户权益须知：" is-link></mt-cell>
		<mt-cell title="理财产品销售协议书：" is-link></mt-cell>
		<div class="field-warn">
			
			<ol>客户声明：
				<li>1. 本人。。。。。。。</li>
				<li>2. 本人确认。。。。。</li>
			</ol>
		</div>
		<mt-cell class="text-center" title="您是否同意其中所有的条款并准备购买？"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">不同意</mt-button>
			<mt-button @click.native="agree" type="primary">同意</mt-button>
		</div>
		<div class="field-warn">
			<ol>温馨提示：
				<li>1. 购买的过程中。。。。</li>
				<li>2. 理财产品。。。。</li>
			</ol>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
export default {
	data() {
		return {
		};
	},
	methods: {
		back() {
			history.back();
		},
		agree() {
			var self = this;
			MessageBox({
			 	title: '提示信息',
			  	message: '您是否同意其中所有的条款并准备购买？',
			  	showCancelButton: true
			}).then(action => {
				if(action === 'confirm'){
					this.$router.push('/finance-products/transaction/product-order/detail/' + this.$route.params.id);
				}
			});
		}
	}
};

</script>

<style lang="scss">

.transaction-product-order-agree{
	.field-warn{
		background-color: #fff;
	}
}

</style>


