<template>
	<div class="finance-products-risk-search">
		<mt-cell title="客户信息查询："></mt-cell>
		<mt-cell title="客户风险承受等级：" v-model="detail.grade" ></mt-cell>
		<mt-cell title="风险等级有效截止日期：" v-model="detail.endDate" ></mt-cell>
		<mt-cell class="colorRed" title="风险等级评估得分：" v-model="detail.score" ></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">重新评估</mt-button>
		</div>
		<div class="field-warn">
			温馨提示：<br>“风险承受能力评估结果有效期为一年，请在结果失效前及时更新您的评估信息。
此评估仅供购买进入产品参考之用，其内容并未包含所有影响投资风险承受能力的因素。如您需要对当前的风险承受能力进行重新评估，请填写《个人投资风险承受能力评估表》。
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
				grade: '',
				endDate: '',
				score: ''
			}
		};
	},
	methods: {
        submit() {
        	this.$router.push('/finance-products/risk-assess');
        },
        back() {
        	history.back();
        },
        getUserRisk(id) {
        	service.getUserRisk({
        		id: id
        	})
        	.then(data => {
        		this.detail = data.data.data;
        	});
        }
	},
	created() {
		this.getUserRisk(this.$route.params.id);
	}
};

</script>

<style lang="scss">
	
.finance-products-risk-search{
	.mint-cell-title{
		width: 5rem;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


