import ajax from '../ajax';
import api from '../api';

export default {

    // 检测客户是否已经签约理财
    checkCustomFinance: data => {
        return ajax.post(api.checkCustomFinance, data);
    },
    /* 风险评估 */
    // 查询客户风险等级
    getUserRisk: data => {
        return ajax.post(api.getUserRisk, data);
    },
    // 客户风险重新评估
    getUserAssess: data => {
        return ajax.post(api.getUserAssess, data);
    },
    // 获取风险评估题目
    getAssessQuestion: data => {
        return ajax.post(api.getAssessQuestion, data);
    },
    /* 产品预约 */
    // 获取账号对应产品列表
    getProductForAccount: data => {
        return ajax.post(api.getProductForAccount, data);
    },
    // 获取产品详情
    getProductDetail: data => {
        return ajax.post(api.getProductDetail, data);
    },
    // 产品购买详情
    getProductBuyDetail: data => {
        return ajax.post(api.getProductBuyDetail, data);
    },
    // 产品购买
    getProductBuy: data => {
        return ajax.post(api.getProductBuy, data);
    },
    // 产品购买成功信息
    getProductBuySuccess: data => {
        return ajax.post(api.getProductBuySuccess, data);
    },
    /* 产品申购 */
    // 获取账号对应申请产品列表
    getProductApplyForAccount: data => {
        return ajax.post(api.getProductApplyForAccount, data);
    },
    // 获取申请产品详情
    getProductApplyDetail: data => {
        return ajax.post(api.getProductApplyDetail, data);
    },
    // 申请产品购买详情
    getProductApplyBuyDetail: data => {
        return ajax.post(api.getProductApplyBuyDetail, data);
    },
    // 申请产品购买
    getProductApplyBuy: data => {
        return ajax.post(api.getProductApplyBuy, data);
    },
    // 申请产品购买成功信息
    getProductApplyBuySuccess: data => {
        return ajax.post(api.getProductApplyBuySuccess, data);
    },
    /* 产品赎回 */
    // 获取账号对应赎回产品列表
    getProductRedeemForAccount: data => {
        return ajax.post(api.getProductRedeemForAccount, data);
    },
    // 获取赎回产品详情
    getProductRedeemDetail: data => {
        return ajax.post(api.getProductRedeemDetail, data);
    },
    // 获取赎回产品赎回确认信息
    getProductRedeemRedeem: data => {
        return ajax.post(api.getProductRedeemRedeem, data);
    },
    // 赎回产品
    redeemProductSubmit: data => {
        return ajax.post(api.redeemProductSubmit, data);
    },
    // 赎回产品成功信息
    redeemProductSuccess: data => {
        return ajax.post(api.redeemProductSuccess, data);
    },
    /* 交易撤单 */
    // 获取账号对应可撤销产品列表
    getOrderBackForAccount: data => {
        return ajax.post(api.getOrderBackForAccount, data);
    },
    // 获取撤销产品详情
    getOrderBackDetail: data => {
        return ajax.post(api.getOrderBackDetail, data);
    },
    // 撤销产品交易
    orderBackSubmit: data => {
        return ajax.post(api.orderBackSubmit, data);
    },
    // 撤销产品交易成功信息
    orderBackSuccess: data => {
        return ajax.post(api.orderBackSuccess, data);
    },
    /* 客户信息查询 */
    getSearchCustomDetail: data => {
        return ajax.post(api.getSearchCustomDetail, data);
    },
    /* 持有产品查询 */
    // 获取账号对应持有产品列表
    getSearchProductForAccount: data => {
        return ajax.post(api.getSearchProductForAccount, data);
    },
    // 获取撤销产品详情
    getSearchProductDetail: data => {
        return ajax.post(api.getSearchProductDetail, data);
    },
    /* 委托交易查询 */
    // 获取账号对应委托交易产品列表
    getSearchTransactionForAccount: data => {
        return ajax.post(api.getSearchTransactionForAccount, data);
    },
    // 获取委托交易产品详情
    getSearchTransactionDetail: data => {
        return ajax.post(api.getSearchTransactionDetail, data);
    }
};



