<template>
	<div class="finance-products">
		<a @click="toRisk('/finance-products/risk')" class="products-item" href="javascript:;">
			<img :src="logo">
			<span>风险评估</span>
		</a>
		<a @click="toFinance('/finance-products/transaction')" class="products-item" href="javascript:;">
			<img :src="logo">
			<span>理财交易</span>
		</a>
		<a @click="toFinance('/finance-products/search')" class="products-item" href="javascript:;">
			<img :src="logo">
			<span>理财查询</span>
		</a>
	</div>
</template>

<script>

import logo from '@/assets/img/icon-bank-logo.png';
import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			logo: logo
		};
	},
	methods: {
		toRisk(type){
			service.checkCustomFinance()
            .then(data => {
                if(!data.data.data){
                    var self = this;
                    MessageBox({
                        title: '提示信息',
                        message: '客户未签约理财，请前往柜面签约。',
                        confirmButtonText: '返回',
                        closeOnClickModal: false
                    }).then(action => {
                    });
                }else{
                    this.$router.push(type);
                }
            });
		},
		toFinance(type) {
			this.$router.push(type);
		}
	}
};

</script>

<style lang="scss">

.finance-products{
	padding-top: 50px;
	.products-item{
		display: inline-block;
		width: 30%;
		margin-bottom: 50px;
		text-align: center;
		img{
			border: 1px solid #26a2ff;
			border-radius: 5px;
			margin-bottom: 15px;
		}
		span{
			display: inline-block;
			width:100%;
			padding: 10px;
			color: #fff;
			background-color: #26a2ff;
			border-radius: 5px;
		}
	}
	.products-item:not(:nth-child(3n-1)){
		margin-left: 2%;
		margin-right: 2%;
	}
}

</style>


