<template>
	<div class="finance-products-risk">
		<mt-cell title="风险评估查询："></mt-cell>
		<mt-cell @click.native="showSelectorPaymentAccount()" title="选择账户：" placeholder="" v-model="detail.account" is-link>
		</mt-cell>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
        <mt-cell title="客户名称：" :value="detail.name"></mt-cell>
        <mt-cell title="证件类型：" :value="detail.idCardType"></mt-cell>
        <mt-cell title="证件号码：" :value="detail.idCard"></mt-cell>
		<div class="field-action">
            <mt-button @click.native="back" type="primary">返回</mt-button>   
            <submit-button @submit="submit(detail.id)" :submitStatus="submitStatus" size="large" value="查询"></submit-button>
		</div>
	</div>
</template>

<script>

export default{
	data() {
		return {
			detail: {
				account: '',
				name: '',
				idCardType: '',
				idCard: ''
			},
			submitStatus: false,
			selectorPaymentAccount: {
                title: '选择账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
    watch: {
        detail: function(item){
            if(item.account){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
    created(){
        this.detail = Object.assign({}, this.$store.state.financeProductsRisk.search);
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail = data.item;
            }, data => {
            });
        },
        submit(id) {
        	this.$router.push('/finance-products/risk-search/' + id);
        },
        back() {
            history.back();
        }
	},
    beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateFinanceProductsRiskSearch', this.detail );
        }else{
            this.$store.dispatch('clearFinanceProductsRiskSearch');
        }
        next( true );
    }
};

</script>

<style lang="scss">
	
.finance-products-risk{
}

</style>


